/*------------------BASE------------------*/
html, body, body div, span, h1, h2, h3, h4, h5, h6, p, img, strong, b, i, ol, ul, li, table, caption, tbody, tfoot, thead, tr, th, td, article, footer, header, menu, nav, section, figure {
	margin: 0;
	padding: 0;
}

img {
	border: none;
}

body {
	background-color: #f2f2f2;
}